@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&family=Oswald:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&family=Oi&family=Oswald:wght@300&family=Reggae+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  margin-top: 0;
  top: 0;
  background-color: #333;
  height: 100vh;
  box-sizing: border-box;
}
.App2 {
  height: 100vh;
  background-color: #183a37;
}
@media only screen and (max-width: 500px) {
  .App {
    background-color: #333;
  }
  .App2 {
    background-color: #183a37;
  }
}
.li_Div {
  display: flex;
  padding: 20px 0px;
}
.Ul {
  display: flex;
  list-style: none;
  justify-content: space-between;
  width: 100%;
}
@media only screen and (max-width: 500px) {
  .Ul {
    margin-top: 10px;
    justify-content: start;
  }
}
.div_2 {
  display: flex;
  flex-direction: row;
}
.name_Div {
  display: flex;
  flex-direction: column;
  height: 88vh;
  width: 15rem;
}
@media only screen and (max-width: 500px) {
  .name_Div {
    height: 88vh;
    width: 15rem;
  }
}
.href_Div {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 20rem;
  margin-left: auto;
}
@media only screen and (max-width: 500px) {
  .href_Div {
    justify-content: center;
    width: 8rem;
  }
}
.welcomeNote-Div {
  display: flex;
  justify-content: center;
}
@media only screen and (max-width: 500px) {
  .welcomeNote-Div {
    display: flex;
    justify-content: center;
    margin-left: 40px;
  }
}
.welcomeNote {
}
@media only screen and (max-width: 500px) {
  .welcomeNote {
    display: flex;
    font-size: 2rem;
    font-family: "Reggae One", cursive;
    color: white;
  }
}
.sub_href {
  display: flex;
  flex-direction: column;
  grid-gap: 2rem;
  gap: 2rem;
  font-size: 2rem;
  font-family: "Oswald", sans-serif;
  color: #333;
}
@media only screen and (max-width: 500px) {
  .sub_href {
    grid-gap: 1rem;
    gap: 1rem;
    font-size: 1rem;
    cursor: pointer;
    margin-left: 40px;
  }
}
.a {
  color: white;
  text-decoration: none;
  margin: 0;
}
.b,
.c,
.d,
.e {
  color: white;
  text-decoration: none;
}
.welcome {
  font-family: "Akaya Telivigala", cursive;
  font-size: 3rem;
  color: white;
}
@media only screen and (max-width: 500px) {
  .welcome {
    font-size: 2rem;
    display: none;
  }
}
.mo {
  display: flex;
  font-family: "Reggae One", cursive;
  font-size: 3rem;
  color: white;
  margin-left: 40px;
}
@media only screen and (max-width: 500px) {
  .mo {
    font-size: 1.5rem;
    margin-left: 10px;
  }
}

.name {
  display: flex;
  margin-top: auto;
  width: 100%;
  font-family: "Oswald", sans-serif;
  font-size: 2rem;
  color: whitesmoke;
  margin-left: 40px;
}
@media only screen and (max-width: 500px) {
  .name {
    font-size: 1.5rem;
    height: 100px;
  }
}
.icon {
  height: 24px;
  font-size: 30px;
  cursor: pointer;
}
@media only screen and (max-width: 500px) {
  .icon {
    display: flex;
    margin-left: 200px;
  }
}
.imageContainer {
  margin-left: 40px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  max-width: -webkit-max-content;
  max-width: -moz-max-content;
  max-width: max-content;
}
.myimage {
  height: 1000px;
  width: 500px;
}
@media only screen and (max-width: 500px) {
  .myimage {
    height: 350px;
    width: 200px;
  }
}
/* //The profile styling starts. */

.profile {
  display: flex;
  justify-content: center;
  background-color: #333;
  height: 100vh;
  margin-top: 0;
  top: 0;
}
.container1 {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 20px;
}
.button-div {
  display: flex;
  margin-top: auto;
  margin-left: auto;
}
.pagebutton {
  padding: 2px 20px;
  font-size: 0.8rem;
  border-radius: 16px;
}
.sec-2 {
  margin-top: 10px;
}
.h1 {
  color: white;
  font-weight: 500;
}
.p {
  font-size: 1rem;
  letter-spacing: 2px;
  margin-top: 20px;
  color: white;
}
.p1 {
  font-size: 1rem;
  color: white;
}
.business-span {
  font-weight: bold;
}

